import React, { Component } from 'react';
import {connect} from 'react-redux';
import {getTimeString,areaToString} from './../../util/helper';
const $ =window.$;
class App extends Component {

  constructor(props){
    super(props);
    this.state={
      videoId:"",
      videoname:"",
      frameTime:"",
      faces:[]
    };
  }

  componentWillReceiveProps(nextProps){
    // if(this.props.areas.length!=nextProps.areas.length){
      if(!nextProps.currentVideo)return;
      let faces=[];
      faces=nextProps.areas.map((obj,index)=>{
        
        let o={
          imageLabel:"",
          imageUrl:"",
          age:"",
          coordinates:obj,
          focused:(obj.z==100)?true:false,
          selected:true
        };
        if(this.state.faces[index]){
          let object=this.state.faces[index];
          o={
            imageLabel:object.imageLabel,
            imageUrl:object.imageUrl,
            age:object.age,
            coordinates:obj,
            focused:(obj.z==100)?true:false,
            selected:true
          };
        }  
        return o;
      });
    let name=nextProps.currentVideo.src.split("/");
    let videoname=name[name.length-1];
    let frametime=getTimeString(nextProps.currentVideo.currentTime);
    this.setState({faces:faces,
                    videoname:name,
                    frameTime:frametime});
  }

  componentDidUpdate(){
    console.log(this.state);
      // document.querySelector(".form input").removeEventListener("focus");
      // listener for onfocus of input remove focus from squares
      $(".form input").focus(()=>{
        $("#canvas").selectAreas("blurAll");
      }); 
  }

  render() {
  return (<div className="col-xs-4 col-sm-4">
      <h3>Form</h3>
      <div className="row scrollbox form">
        <form action="" onSubmit={(e)=>{
          e.preventDefault();
          this.submitForm();
        }}>
       <table className="table">
         <tbody>
          {this.state.faces.map((obj,index)=>{return (
          <tr key={index} className={obj.focused?"row-select":""}>
             <td><input type="checkbox" name="" id="" checked={obj.selected}
              onChange={()=>this.onSelectToggle(index)}
             /></td>
             <td>
             <div className="form-group row no-mrg">
                                <label htmlFor="frametime" className="col-xs-3 control-label">Image Url</label>
              
                                <div className="col-xs-9">
                                  <select name="imageurl"
                                  id="imageurl"
                                  className="form-control"
                                  value={obj.imageUrl} 
                                        onChange={(e)=>this.onChange(index,'imageUrl',e.target.value)} 
                                  placeholder="Image Url"
                                  required 
                                  >
                                  <option value="">-Select Image-</option>
                                  {this.props.images.map((str,index)=>{
                                    let name=str.split("/");
                                    str=name[name.length-1];
                                    return <option value={str} key={index}>{str}</option>
                                  })}
                                  </select>
                                </div>
              </div>
              <div className="form-group row no-mrg">
                                      <label htmlFor="frametime" className="col-xs-3 control-label">Image Label</label>
                    
                                      <div className="col-xs-9">
                                        <input type="text" 
                                        name="frametime" 
                                        id="frametime"
                                        className="form-control"
                                        value={obj.imageLabel} 
                                        onChange={(e)=>this.onChange(index,'imageLabel',e.target.value)}
                                        placeholder="Image Label"
                                        required 
                                        />
                                      </div>
              </div>
              <div className="form-group row no-mrg">
                                      <label htmlFor="frametime" className="col-xs-3 control-label">Coordinates</label>
                    
                                      <div className="col-xs-9">
                                        <div className="coordinates">{areaToString(obj.coordinates)}</div>
                                      </div>
              </div>
              
              <div className="form-group row no-mrg">
                                      <label htmlFor="frametime" className="col-xs-3 control-label">Age</label>
                    
                                      <div className="col-xs-9">
                                        <input type="number" 
                                        name="frametime" 
                                        id="frametime"
                                        className="form-control"
                                        value={obj.age} 
                                        onChange={(e)=>this.onChange(index,'age',e.target.value)} 
                                        placeholder="Age"
                                        required 
                                        />
                                      </div>
              </div>
                  </td>
          </tr>)})}

           {/* <tr>
             <td> <input type="checkbox" name="" id=""/> </td>
             <td>
               <p className="col-xs-12">
                <span className="dark">Image Url : </span> {frameObj.imageUrl} <br/> 
                <span className="dark">Image Label : </span> {frameObj.imageLabel} <br/>
                <span className="dark">Coordinates : </span> {frameObj.coordinates} <br/>
                <span className="dark">Age : </span> {frameObj.age}
                </p>  
             </td>
              </tr>
              <tr>
                <td> <input type="checkbox" name="" id=""/> </td>
                <td>
                  <p className="col-xs-12">
                    <span className="dark">Image Url : </span> {frameObj.imageUrl} <br/> 
                    <span className="dark">Image Label : </span> {frameObj.imageLabel} <br/>
                    <span className="dark">Coordinates : </span> {frameObj.coordinates} <br/>
                    <span className="dark">Age : </span> {frameObj.age}
                    </p>  
                </td>
              </tr>*/}

            </tbody>
          </table> 
         <hr/>     
        <div className="text-center">
              <button className="btn btn-info btn-lg">Submit</button>
        </div>
        </form>
      </div>   
    </div>);
  }

  submitForm(){

  }

  onChange(index,key,val){
    let faces=JSON.parse(JSON.stringify(this.state.faces));
    faces[index][key]=val;
    this.setState({faces});
  }

  onSelectToggle(index){
    let faces=JSON.parse(JSON.stringify(this.state.faces));
    let selected=faces[index].selected;
    if(selected)
      faces[index].selected=false;
    else
     faces[index].selected=true;
    this.setState({faces});
  }

}

export default connect((state)=>{
  return {
    areas:state.areas,
    currentVideo:state.currentVideo,
    videos:state.videos,
    images:state.images
  };
},null)(App);



