import {dedudancyRemove} from './../util/helper';
// import swal from 'sweetalert2';
import URL from './../util/ApiConst';
const $=window.$;

export const GET_IMAGES_LIST="GET_IMAGES_LIST";
export const UPLOAD_IMAGE="UPLOAD_IMAGE";

function loadImagesListJSON(dispatch){
    console.log("list req")
     return $.ajax({
        url:URL.GET_IMAGES_LIST,       
      }).done(data=>{
          console.log(data);
        if(typeof data=="string")data=JSON.parse(data);
        dispatch(returnGetImagesListAction( data.images ));
      })
      .fail((err,e,xhr)=>{
        console.log(xhr);
      });
}


function returnGetImagesListAction(images){
    
    console.log("list " , images)
    return {
        type:GET_IMAGES_LIST,
        images
    }
}


export function loadImagesList(){
    // console.log("image loading request")
 return function(dispatch){
     return loadImagesListJSON(dispatch);  
 }
}

// upload image

function uploadImageJson(dispatch,image){
    var formData = new FormData();
    formData.set("image",image);
    return $.ajax({
        type:'POST',
        url:URL.UPLOAD_IMAGE,
        data:formData,
        cache:false,
        contentType: false,
        processData: false
    }).done((data)=>{
        console.log(data);
        dispatch(returnGetImagesListAction(data.url));
    }).fail((err,e,xhr)=>{
        console.log(xhr);
    });
}

function returnUploadImageAction(image){
    return {
        type:UPLOAD_IMAGE,
        image
    }
}

export function uploadImage(image){
    return function(dispatch){
        return uploadImageJson(dispatch,image);
    }
}


