import React, { Component } from 'react';
import {selectVideo} from './../../actions/VideoActions';
const $ =window.$;
class VideoBox extends Component {
  render() {
    let selected=this.refs.video&&(this.refs.video==this.props.currentVideo);
    // console.log(selected);
  return (<div>
        <div className={selected?"thumbnail selected":"thumbnail "}
        onClick={()=>{this.props.selectVideo(this.refs.video)}}>
                        <video src={this.props.src} className="img-responsive" ref="video"></video>
                        <div className="video-overlay">
                            <i className={selected&&this.refs.video?"fa fa-check":"fa fa-play"}></i>
                        </div>
                </div>
    </div>);
  }
}

export default VideoBox;



