
export function dedudancyRemove(list){
    if(typeof list=="string")list=JSON.parse(list);
    var nonRedundantList=[];
    for(let x in list){
      if((nonRedundantList.indexOf(list[x])==-1) 
         && typeof list[x] =="string")
      nonRedundantList.push(list[x]);
    }
    return nonRedundantList;
  }

export function objToArray(obj){
    let arr=[]; 
    for(let x in obj){
        arr.push(obj[x])
    }
    return arr;
}

export function getTimeString(audioPlayer,time){
    let hr=Math.floor(time/(36000)),
    min=Math.floor(time/60),
    sec= Math.ceil(time%60),
    millisec=Math.ceil((time%1)*100);
    // console.log(time)
    let str=hr+":"+min+":"+sec+":"+millisec;
     return isNaN(audioPlayer.duration)?"00:00:00:00":str;
}

export function areaToString(area) {
    return (typeof area.id === "undefined" ? "" : ("Face "+area.id + " : (")) + Math.ceil(area.x) + ',' + Math.ceil(area.y) + ')   ' + area.width + 'x' + area.height;
}

export function generateId(){
    let chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZ-abcdefghijklmnopqrstuvwxyz0123456789";
    let sb = [];
    for (let i = 0; i < 30; i++) {
        let c = chars[Math.floor(Math.random()*30)];
        sb.push(c);
    }
    let random= sb.join("");
    console.log(random);
    return random;
  }


