import React, { Component } from 'react';
import { connect } from "react-redux";
import VideoControls from './VideoControls';
import {updateAreas} from "./../../actions/FacesActions";
import {getTimeString} from './../../util/helper';
const $ =window.$;
class App extends Component {
  
  constructor(props){
    super(props);
    this.state={
      width:640,
      height:360,
      frameTime:(1/30)
    };
    this.areas=[];
  }

  componentWillReceiveProps(nextProps){
    // console.log("method props ",nextProps)
    
  }
  
  render() {
    return (<div className="inline-block">
      {/* <div className="canvas-container"> */}
      <canvas ref="canvas" id="canvas" width={this.state.width}  height={this.state.height} ></canvas>
      {/* <FaceObjectContainer/> */}
      <VideoControls {...this.props}
        playOrPause={()=>this.playOrPause()}
        prevFrame={()=>this.prevFrame()}
        nextFrame={()=>this.nextFrame()}
        volumeManager={(val)=>this.volumeManager(val)}
      />
      </div> 
    );
  }


  shouldComponentUpdate(nextProps, nextState){
    if(this.props.currentVideo!=nextProps.currentVideo)
    return true;
    else
    return false;
  }

  componentDidUpdate(prevProps,prevState){
    let canvas = this.refs.canvas;
    let ctx = canvas.getContext('2d');
    let video =this.props.currentVideo;
    
    //time update listener -- repaint method
    this.props.currentVideo.ontimeupdate=()=>{
      this.draw(canvas,ctx,video);
      this.updatePlayerTime();
    }
 
    // reset previous video listeners
    if(prevProps.currentVideo){
      $("#canvas").selectAreas('destroy');
      prevProps.currentVideo.currentTime=0
      prevProps.currentVideo.ontimeupdate=null;
    };

    // add new video listener
    if(this.props.currentVideo){
     video.currentTime=this.state.frameTime;
        var facePlugin=$('#canvas').selectAreas({
          onChanged:(e,id,areas)=>{
            this.onChangeArea(e,id,areas);
          }, 
          minSize: [30, 30],
          maxAreas: 3,
          aspectRatio:1,
          // allowNudge:false,
          areas:[]
      });

      let src=this.props.currentVideo.src.split("/");
      let name=src[src.length-1];
      document.getElementById("player_video_name").innerHTML=name;
    }

  }

  onChangeArea(event,id,areas){
    // console.log(id)
    this.props.updateAreas(areas);
  }

  volumeManager(value){
    let videoPlayer=this.props.currentVideo;
    if(!videoPlayer)return;
    videoPlayer.volume=value/100; 
    if(value<=50){
      $("#volume-icon").removeClass();
      $("#volume-icon").addClass("fa fa-volume-down");
    }
    else if(value<0){
      $("#volume-icon").removeClass();
      $("#volume-icon").addClass("fa fa-volume-off");
    }
    else{
      $("#volume-icon").removeClass();
      $("#volume-icon").addClass("fa fa-volume-up");
    }
    //  console.log(value);
  }

  draw(canvas,ctx,video){
    ctx.clearRect(0,0,canvas.width,canvas.height);
    ctx.drawImage(video,0,0,this.state.width,this.state.height);
    ctx.font="20px Georgia";
    // ctx.rect(++this.c,++this.c,50,50);
    // ctx.fillText("hello",150,80);
    // ctx.stroke();
  }

  nextFrame(){
    if(this.props.currentVideo){
    let video=this.props.currentVideo;
    video.currentTime= Math.min(video.duration, video.currentTime + this.state.frameTime);
    }
  }

  
  prevFrame(){
    if(this.props.currentVideo){
    let video=this.props.currentVideo;
    video.currentTime= Math.max(0, video.currentTime - this.state.frameTime);
    }
  }

  play(){
    if(this.props.currentVideo){
      let that=this;

      this.props.currentVideo.addEventListener("play",function(){
        setInterval(()=>{
        if(!this.ended&&!this.paused)
        that.draw(that.refs.canvas,that.refs.canvas.getContext("2d"),this);    
        },0);  
      });
      this.props.currentVideo.play();
    }
  }

  pause(){
    if(this.props.currentVideo){
      if(!this.props.currentVideo.paused)
      this.props.currentVideo.pause();
      }
   }

   playOrPause(){
     let videoPlayer=this.props.currentVideo;
     if(!videoPlayer)
     return;
     if(videoPlayer.paused)
    {
        this.play();
        $("#playorpause").html('<i class="fa fa-pause" aria-hidden="true"></i>');
        
    }
    else{
        this.pause();
        $("#playorpause").html('<i class="fa fa-play" aria-hidden="true"></i>');
    }
   }

   updatePlayerTime(){
     let videoPlayer=this.props.currentVideo;
    $("#current_time").html(getTimeString(videoPlayer,videoPlayer.currentTime));
    // $("#video_slider")[0].value=isNaN(videoPlayer.duration)?0:parseInt((videoPlayer.currentTime/videoPlayer.duration)*100);
    if(videoPlayer.currentTime<1)
    $("#end_time").html(getTimeString(videoPlayer,videoPlayer.duration));
    }

    componentDidMount(){
      let canvas=this.refs.canvas;
      let ctx=canvas.getContext("2d");
    }


  }

export default connect(state=>{return {
    currentVideo:state.currentVideo,
    areas:state.areas
};
},{
  updateAreas
})(App);



