import React, { Component } from 'react';
const $ =window.$;
class App extends Component {
  render() {
  return (<div>
          <div className="modal fade" id="uploadFile" tabIndex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                  <h4 className="modal-title" id="myModalLabel">Upload File - Image/Video</h4>
                </div>
                <div className="modal-body">
                  <div className="row">
                  <div className="col-xs-12">
                  <div className="form-group">
                    <div className="col-xs-3">
                     <label htmlFor="File">Select File
                     </label>  
                    </div>
                    <div className="col-xs-9">
                      <input type="file" name="" id="" accept="image/*,video/mp4,video/mkv,video/x-m4v,video/*"/>
                    </div>
                  </div>
                  <div className="col-xs-12 text-center">
                    <button className="btn btn-primary"> <i className="fa fa-upload"></i> Upload</button>
                  </div>
                  </div>
                  </div>
                </div>
                <div className="modal-footer">
                  <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                  <button type="button" className="btn btn-primary">Background</button>
                </div>
              </div>
            </div>
          </div>
    </div>);
  }
}

export default App;



