import React, { Component } from 'react';
import {enableVideoView,enableImageView} from './../../actions/FilesNavbarActions.js';
import { connect } from "react-redux";

const $ =window.$;
class NavBarHeader extends Component {
    constructor(props){
        super(props);
        this.state={
            searchBoxOpen:true,
        };
    }

  render() {
  return (<div className="row">
        <div className="header col-xs-12">
            <div className="pull-left btn">
              <i className="fa fa-arrow-left navbar-close"></i> 
            </div>

            <button className="btn btn-info"  data-toggle="modal" data-target="#uploadFile"> <i className="fa fa-upload"></i> </button>

            <ul className="nav nav-tabs pull-right">
               <li role="presentation" className={this.props.imageViewEnabled?"active":""}>
                <a onClick={()=>this.props.enableImageView()}> <i className="fa fa-image"></i>  </a></li>
               <li role="presentation" className={this.props.imageViewEnabled?"":"active"} >
                <a onClick={()=>this.props.enableVideoView()}> <i className="fa fa-file-video-o"></i> </a></li>
            </ul>
                    </div>   
        {/* <div className={this.state.searchBoxOpen?"files-box search_box":"files-box search_box hide"} style={{borderTop:"none"}}>
             <input type="text" className="form-control search_box" placeholder="Search here"/>
        </div> */}
    </div>);
  }
}

export default connect((state)=>{
  return {imageViewEnabled:state.imageViewEnabled } 
},
{
   enableImageView,
   enableVideoView 
})
(NavBarHeader);



