import {dedudancyRemove} from './../util/helper';
// import swal from 'sweetalert2';
import URL from './../util/ApiConst';
const $=window.$;

export const GET_VIDEOS_LIST="GET_VIDEOS_LIST";
export const UPLOAD_VIDEO="UPLOAD_VIDEO";


function loadVideosListJSON(dispatch){
    return $.ajax({
        url:URL.GET_VIDEOS_LIST,       
      }).done(data=>{
        if(typeof data=="string")data=JSON.parse(data);
        dispatch(returnGetVideosListAction( data.videos));
      })
      .fail((err,e,xhr)=>{
        console.log(xhr);
      });
}


function returnGetVideosListAction(videos){
    return {
        type:GET_VIDEOS_LIST,
        videos
    }
}


export function loadVideosList(){
 return function(dispatch){
     return loadVideosListJSON(dispatch);  
 }
}

// upload video

function uploadVideoJson(dispatch,video){
    var formData = new FormData();
    formData.set("video",video);
    return $.ajax({
        type:'POST',
        url:URL.UPLOAD_VIDEO,
        data:formData,
        cache:false,
        contentType: false,
        processData: false
    }).done((data)=>{
        console.log(data);
        dispatch(returnGetVideosListAction(data.url));
    }).fail((err,e,xhr)=>{
        console.log(xhr);
    });
}

function returnUploadVideoAction(video){
    return {
        type:UPLOAD_VIDEO,
        video
    }
}

export function uploadVideo(video){
    return function(dispatch){
        return uploadVideoJson(dispatch,video);
    }
}

// ********************************************************************

export const SELECT_VIDEO="SELECT_VIDEO";

export function selectVideo(video) {
    return {
        type:SELECT_VIDEO,
        video
    }
}


