import React, { Component } from 'react';
import VideoBox from './VideoBox';
import { connect } from "react-redux";
import {loadVideosList,selectVideo} from './../../actions/VideoActions';
const $ =window.$;


const demo="./../a.mkv";

class VideosContainer extends Component {
  render(){
  return (
    <div className="row">
    <div className="col-xs-12 scrollbox videos-box">
    {this.props.videos.map((obj,index)=>{
      return <VideoBox src={obj} selectVideo={this.props.selectVideo.bind(this)}
       currentVideo={this.props.currentVideo}
       key={index}/>;
    })}
    </div>
    </div>);
  }

  
  componentDidMount(){
    if(!this.props.videos.length)
      this.props.loadVideosList();
  }
}

export default connect((state)=>{
  return {
    videos:state.videos,
    currentVideo:state.currentVideo
  }
},{
  loadVideosList,
  selectVideo
})(VideosContainer);



