import React, { Component } from 'react';
import ImageBox from './ImageBox';
import { connect } from "react-redux";
import {loadImagesList} from './../../actions/ImageActions';
const $ =window.$;

const demo="./../1.jpg";

class ImagesContainer extends Component {
  render() {
  return (<div className="row">
  <div className="col-xs-12 scrollbox images-box">
      {this.props.images.map((obj,index)=>{
        return <ImageBox src={obj} selected={""} key={index}/>;
      })}
    </div>
    </div>);
  }
  componentDidMount(){
     
      if(!this.props.images.length)
        this.props.loadImagesList();  
  }
}

export default connect((state)=>{
  return {
    images:state.images
  }
},{
  loadImagesList
})(ImagesContainer);


