
import {GET_IMAGES_LIST,UPLOAD_IMAGE} from './../actions/ImageActions';

import * as CONSTANTS from './../util/constants';

var JobsList=[];

export function imagesReducer(state=[],action){
    // console.log(action)
    switch(action.type){
        
        case GET_IMAGES_LIST :return action.images;
        case UPLOAD_IMAGE:return [action.image,...state];

        default: return state;
    }
}



