import React, { Component } from 'react';
import NavBarHeader from './NavBarHeader';
import ImagesContainer from './../ImagesContainer/ImagesContainer';
import VideosContainer from './../VideoContainer/VidoesContainer';
import {connect} from 'react-redux';

const $ =window.$;
class NavBar extends Component {
  render() {
  return (
  <div className="col-xs-12 col-sm-2 navbar padd_lr_5">
    <NavBarHeader/>
    <div className={this.props.imageViewEnabled?"":"hide"}><ImagesContainer/></div>
    <div className={this.props.imageViewEnabled?"hide":""}><VideosContainer/></div>
  </div>);
  }
}

export default connect((state)=>{
  return {imageViewEnabled:state.imageViewEnabled}  
},
null)(NavBar);



