export const IMAGE_VIEW_ACTIVE="IMAGE_VIEW_ACTIVE";
export const VIDEO_VIEW_ACTIVE="VIDEO_VIEW_ACTIVE";

export function enableImageView(){
    return {
        type:IMAGE_VIEW_ACTIVE
    };
}

export function enableVideoView(){
    return {
        type:VIDEO_VIEW_ACTIVE
    };
}


