import {IMAGE_VIEW_ACTIVE,VIDEO_VIEW_ACTIVE} from './../actions/FilesNavbarActions';

export function setImageOrVideoView(state=true,action){
    switch(action.type){
        case IMAGE_VIEW_ACTIVE:return true;
        case VIDEO_VIEW_ACTIVE:return false;
        default:return state;
    }
}


