import React, { Component } from 'react';
const $ =window.$;
class App extends Component {
    render(){
        return (
    <div className="player row no-mrg">

        <div className="video_player">
             
             <div className="video-controls pull-left">
                <a id="prevframe" onClick={()=>this.props.prevFrame()} >
                    <i className="fa fa-step-backward" aria-hidden="true"></i></a>
                <a id="playorpause" onClick={()=>this.props.playOrPause()}>
                    <i className="fa fa-play-circle" aria-hidden="true"></i>
                </a>
                <a id="nextframe" onClick={()=>this.props.nextFrame()}>
                    <i className="fa fa-step-forward" aria-hidden="true"></i></a>
             </div>

             <div className="video_progress pull-left">
             <div className="player_video_details text-center"> 
                    <span id="player_video_name">-- Choose a video --</span>   
                </div>
                <div className="timing-info text-center">
               <span id="current_time" className="pull-left video_time">00:00:00</span> /
               <span id="end_time" className="video_time">00:00:00</span>
               </div>
             </div>                

             <div className="video-controls pull-right">
                <a id="volume_box" className={this.props.currentVideo?"":"hide"}>
                <div id="volume_manager" className="none">
                    <input type="range" min="0" max="100"  
                    orient="vertical" 
                    onInput={e=>this.props.volumeManager(e.target.value)} 
                    id="volume_slider"/>
                    </div>
                   <i id="volume-icon" className="fa fa-volume-up" aria-hidden="true"></i>
                </a>
             </div>
        </div>
    
    </div>);
    }
}

export default App;



