
import {GET_VIDEOS_LIST,UPLOAD_VIDEO,SELECT_VIDEO} from './../actions/VideoActions';

import * as CONSTANTS from './../util/constants';

export function videosReducer(state=[],action){
    // console.log(action)
    switch(action.type){
        
        case GET_VIDEOS_LIST :return action.videos;
        case UPLOAD_VIDEO:return [action.video,...state];

        default: return state;
    }
}

export function selectVideo(state=null,action) {
    switch(action.type){
        case SELECT_VIDEO:return action.video;
        default:return state;
    };
}


