import {combineReducers} from 'redux';
import {videosReducer as videos,
    selectVideo as currentVideo} from './videosReducer';
import {imagesReducer as images} from './imagesReducer';
import {setImageOrVideoView as imageViewEnabled} from './FilesNavbarReducer';
import {updateAreas as areas} from "./FacesReducer";
const rootReducer = combineReducers({
   videos,
   images,
   imageViewEnabled,
   currentVideo,
   areas
});
export default rootReducer;



