import React, { Component } from 'react';
const $ =window.$;
class ImageBox extends Component {
  render() {
  return (<div className="col-xs-6" style={{padding:"0px 5px"}}>
   <div className={this.props.selected?"thumbnail selected":"thumbnail "}>
             <img src={this.props.src} className="img-responsive" alt=""/>
             <div className="img-overlay">
                <i className="fa fa-check"></i>
              </div>
        </div>
    </div>);
  }
}

export default ImageBox;



