import React, { Component } from 'react';
import MainCanvas from './MainCanvas';
import CanvasOptions from './CanvasOptions';
const $ =window.$;
class App extends Component {

  constructor(props){
    super(props);
  }

  render() {
  return (<div className="col-xs-12 col-sm-6">
    <div className="row no-mrg">
      <MainCanvas/>
      {/* <CanvasOptions/> */}
    </div></div>);
  }
}

export default App;



